<template>
    <div class=" parent">

        <img class="child" v-bind:src="this.img_canvas[Slide]" />
        <img v-bind:src="this.image[Slide]" style="width: 100%;">

    </div>
</template>
  
<script scope>
import axios from 'axios';

export default {
    name: "Image",
    component: {

    },
    props: {

        _data: Object,
    },

    computed: {

        Slide() {
            let slide = this._data.num - 1;

            if (this._data.num > this.data.length) {

                slide = 0;

            }
            return slide
        },
    },
    data() {
        return {
            data: [],
            image: [],
            img: null,
            img_canvas: [],
            localhost: window.location.host
        }
    },
    mounted() {
        axios.get(`http://localhost:3000/services/arm_service/searchshirt/${this._data._id}`)
            .then(response => {
                this.data = response.data;
                for (let i in this.data) {
                    this.image.push(require(`@/assets/img/${this.data[i].property._type}front.png`));

                    this.img_canvas.push(`http://localhost:3000/images/${this.data[i].shirt_path.front}.png`)
                }
            })
            .catch(error => {
                console.log(error);
            });

    },
    methods: {
    }
};
</script>
  
  
<style scoped>
/* we will explain what these classes do next! */


.parent {
    position: relative;
}

.child {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40%;
    height: 70%;
    text-align: center;
}
</style>